import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { addToCart } from "../../../actions/cart";
import ProductCard from "../../../components/features/product";
import QuickView from "../../../components/features/product/QuickViewNew";
import ChooseVariationModal from "../../../components/features/modal/choose-variation";
import { getRelatedProducts } from "../../../api/products";
import { useTranslation } from "react-i18next";
import market from "../../../config/market";
import SimpleShimmerProducts from "../../../components/features/loaders/SimpleShimmerProducts";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { relatedSlider, relatedSliderTwo } from "../../../components/options";

const queryString = require("qs");

function RelatedProducts({ addToCart, product, setIsEmpty }) {
	const { t } = useTranslation();
	const [loading, setLoading] = useState(true);
	let mount = true;

	const [relatedProducts, setRelatedProducts] = useState([]);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 1 } },
	});

	const initEffect = async () => {
		const strQuery = queryString.stringify({
			page: 1,
			size: 10,
		});
		let { variations } = await getRelatedProducts(
			product.product,
			strQuery
		);
		if (mount) {
			variations = variations.filter(
				(variation) => variation.stock.id !== product.data.stock.id
			);
			if (variations.length > 0 && setIsEmpty) {
				setIsEmpty(false);
			}
			setRelatedProducts(variations);
			setLoading(false);
		}
	};

	useEffect(() => {
		initEffect();
		return () => {
			mount = false;
		};
	}, [product.id]);

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct,
		});
	}

	return (
		<div className="mt-5 relation-products">
			<div className="row">
				<div className="col-lg-12 col-md-12 col-12 mx-auto">
					<h2 className="title text-center mb-4">
						{t("similar-products")}
					</h2>
					{loading ? (
						<SimpleShimmerProducts size={2} />
					) : (
						<>
							{relatedProducts.length > 0 ? (
								<>
									<Splide
										adClass="owl-simple carousel-equal-height carousel-with-shadow"
										options={
											market.config.home === 6
												? {
														...relatedSliderTwo,
														arrows: false,
												  }
												: relatedSlider
										}
										carouselId="relatedProducts"
									>
										{relatedProducts.map((item, index) => (
											<SplideSlide
												key={`product-${index}`}
											>
												<ProductCard
													product={item}
													onAddToCart={(
														dataProduct
													) => {
														onAddToCart(
															dataProduct
														);
													}}
												/>
											</SplideSlide>
										))}
									</Splide>
									{/* )} */}
								</>
							) : (
								<p className="bold text-center">
									{t("nothing-found")}
								</p>
							)}
						</>
					)}
					<QuickView />
					<ChooseVariationModal
						setModal={setModal}
						modal={modal}
						addToCart={addToCart}
					/>
				</div>
			</div>
		</div>
	);
}

export default connect(null, {
	addToCart,
})(RelatedProducts);
