import React, { useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
// import { toast } from "react-toastify";

import { formatterCurrency } from "../../../../utils/string";
import PropertiesView from "../media/components/PropertiesView";

import ProductBrandAttrAndSupplier from "./components/ProductBrandAttrAndSupplier";
import { useTranslation } from "react-i18next";
import addToCartCheck from "../../../../components/features/product/addToCartCheck";
import market from "../../../../config/market";
import { setChoosenProduct } from "../../../../actions/choosenProducts";
// import InputChangerCountAddCart from "../../../../components/common/InputChangerCountAddCart";
import BuyInstallments from "../../../../components/features/modal/BuyIstallments";

function ProductDetailFive(props) {
	const { product, onComplated, token } = props;
	const { t } = useTranslation();
	const { cart } = useSelector(state => state.cartlist);
	// const haveCart = cart.find(p => p.data.stock.id === product.data.stock.id);
	const dispatch = useDispatch();
	const [quantity, setQuantity] = useState(1);
	const [enable, setEnable] = useState(true);
	function setLoading() {}
	const { productList } = useSelector(state => state.choosenProducts);
	const haveChoosen = productList.find(
		p => p.stock.id === product.data.stock.id
	);
	const { cartDescription } = useSelector(state => state.marketData.settings);

	const [buyInstallmentModal, setBuyInstallmentModal] = useState(false);
	const addToCartHandler = () => {
		addToCartCheck(product, quantity, token, setLoading, dispatch, cart);
	};
	// const max_count = parseInt(product.data.count);
	function onComplatedMiddle(variationId) {
		if (variationId) {
			setEnable(true);
			onComplated(variationId);
		} else {
			setEnable(false);
		}
	}
	// function onChange(qty) {
	// 	if (parseInt(qty) > max_count) {
	// 		toast.error(t("amount-balance"), {
	// 			position: "bottom-left"
	// 		});
	// 	}
	// }

	useEffect(() => {
		setQuantity(1);
	}, [product]);

	return (
		<div className="product-details">
			<div className="product-price">
				<span className="new-price">
					{formatterCurrency(product.data.newPrice)}
					{product.data.discountPercent > 0 ? (
						<span>{product.data.discountPercent}%</span>
					) : null}
				</span>
				{0 < product.data.discount ? (
					<span className="old-price">
						{formatterCurrency(product.data.oldPrice)}
					</span>
				) : null}
			</div>
			<div className="installment">
				<div className="installment-price">
					<img src={market.config.icons.bankLogo} alt="bank-icon" />
					<h5>
						<p>
							{formatterCurrency(
								(product.data.newPrice * 1.29) / 12
							)}
						</p>
						<span>12 Месяцев</span>
					</h5>
				</div>

				<button
					className="installment-btn"
					disabled={true}
					onClick={() => {
						setBuyInstallmentModal(!buyInstallmentModal);
					}}
				>
					<span>Рассрочка</span>
					<p>{t("soon")}</p>
				</button>
			</div>

			<div className="properties">
				{product.data.variation.properties ? (
					<PropertiesView
						product={product}
						onComplated={onComplatedMiddle}
						setEnable={setEnable}
						enable={enable}
					/>
				) : null}
			</div>
			{product.product.minOrder ? (
				<div className="min-order">
					<h5 className="text">
						Количество:{" "}
						<span>
							{product.product.minOrder < 3 ? (
								<span>{t("less")}</span>
							) : (
								product.product.minOrder
							)}
						</span>
					</h5>
				</div>
			) : null}
			{/* <div className="mobile-top" /> */}
			<div className="favorite-with-btn-cart">
				{/* {haveCart ? (
								<div className="product-details-quantity">
									<InputChangerCountAddCart
										initValue={haveCart.count}
										addToCartHandler={addToCartHandler}
										onChange={value =>
											onChange(
												value,
												product.data.variation.id
											)
										}
										variationId={product.data.variation.id}
										token={token}
									/>
								</div>
							) : ( */}
				<button
					className="cart-btn"
					onClick={() => {
						addToCartHandler();
					}}
				>
					<img src={market.config.icons.cart} alt="cart" />
					{t("add-cart")}
				</button>
				{/* )} */}
				<span
					className={`d-flex align-items-center favourites-button ${
						haveChoosen ? `choosen` : ``
					}`}
					onClick={() => dispatch(setChoosenProduct(product.data))}
					style={{
						cursor: "pointer"
					}}
				>
					<img
						src={market.config.icons.favourites}
						alt="favourites"
					/>
				</span>
			</div>

			<div
				className="aborting-and-delivery"
				style={{ display: "flex", alignItems: "center" }}
				dangerouslySetInnerHTML={{
					__html: cartDescription
				}}
			/>

			<div className="details-footer">
				<ProductBrandAttrAndSupplier product={product} />
			</div>

			{buyInstallmentModal ? (
				<BuyInstallments
					buyInstallmentModal={buyInstallmentModal}
					setBuyInstallmentModal={setBuyInstallmentModal}
				/>
			) : null}
		</div>
	);
}

export default connect(state => ({
	settings: state.marketData.settings,
	token: state.user.tokenData?.token
}))(ProductDetailFive);
