import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { Helmet } from "react-helmet";
import { connect, useDispatch, useSelector } from "react-redux";
import MediaOne from "./partials/media/media-one";
import ProductDetailTwo from "./partials/details/detail-two";
import DescOne from "./partials/description/desc-one";
import Breadcrumb from "../../components/common/breadcrumb";
import { productGallery } from "../../utils/index";
import {
	getProductCategoryPath,
	getProductPromotion,
	// getProductsPromotions,
	getVariation,
} from "../../api/products";
import ProductPageSkleton from "../../components/features/loaders/ProductPageSkleton";
import LikeProductsSupplier from "./partials/LikeProductsSupplier";
import { onComplatedChoosingProp } from "./helpers";
import market from "../../config/market";
import BackButton from "../../components/common/BackButton";
import { useTranslation } from "react-i18next";
import BuyOneClick from "../../components/features/modal/BuyOneClick";
import { openModal } from "../../actions/modal";
import { isIEBrowser, isEdgeBrowser } from "../../utils";
import addToCartCheck from "../../components/features/product/addToCartCheck";
import Loader from "../../components/features/loaders/loader";
import ProductCharacteristic from "../../components/features/modal/ProductCharacteristic";
import PhoneInput from "react-phone-number-input";
import { calculateYear, getImageUrl } from "../../utils/oxMethods";
import AcseptModal from "../../components/features/modal/AcseptModal";
import { removeFromCartState } from "../../actions/cart";
import { getCarts, removeToCartApi } from "../../api/cart";
import { toast } from "react-toastify";
import { dt } from "../../i18n";
import CrossSellProducts from "./CrossSellProducts";
function ProductPageFour(props) {
	const [acseptModal, setAcseptModal] = useState({ item: null, open: false });
	const productId = props.match.params.id;
	const { t } = useTranslation();
	const history = useHistory();
	const { settings } = useSelector((state) => state.marketData);
	const address = useSelector((state) =>
		state.user.location?.address?.administrativeArea
			? state.user.location.address.administrativeArea
			: state.user.location?.address?.name
	);
	const [crumb, setCrumb] = useState([]);
	const [product, setProduct] = useState({ product: { id: -1 } });
	const [loading, setLoading] = useState(true);
	const [loadingProduct, setLoadingProduct] = useState(false);
	const [selectVar] = useState(true);
	const [modalBOC, setModalBOC] = useState({ open: false });
	const location = useSelector((state) => state.user.location);
	const dispatch = useDispatch();
	const [quantity, setQuantity] = useState(1);
	const [enable, setEnable] = useState(selectVar);
	const { cart } = useSelector((state) => state.cartlist);
	const { token } = useSelector((state) => state.user?.tokenData);
	const [isOpen, setIsOpen] = useState({ open: false });
	const [BOC, setBOC] = useState(false);
	const [value, setValue] = useState();
	const [supplier, setSupplier] = useState();
	const [promotion, setPromotion] = useState();
	const sells = useSelector((state) => state.cartlist.cart);
	const addToCartHandler = () => {
		addToCartCheck(product, quantity, token, setLoading, dispatch, cart);
	};
	function removeFromCart(id, variationId) {
		dispatch(removeFromCartState(variationId));
		removeToCartApi(id, token).catch((e) => {
			toast.error(e.response?.data?.message, { position: "top-left" });
		});
	}
	async function isClicked(product) {
		if (token) {
			const res = await getCarts(token);
			for (const sell of res) {
				for (const record of sell.sellRecords) {
					if (!record.deleted) {
						removeFromCart(record.id, record.variation, () => {});
					}
				}
			}
			addToCartCheck(
				product,
				quantity,
				token,
				setLoading,
				dispatch,
				cart
			);
		} else {
			await sells.forEach((sell) => {
				if (sell.suppliers[0].id !== product.suppliers[0].id) {
					dispatch(removeFromCartState(sell.data.variation.id));
				}
			});
			addToCartCheck(
				product,
				quantity,
				token,
				setLoading,
				dispatch,
				cart
			);
		}
	}

	const addToCartSingleSupplier = () => {
		if (cart.length < 1) {
			addToCartCheck(
				product,
				quantity,
				token,
				setLoading,
				dispatch,
				cart
			);
		} else {
			if (product.suppliers[0].id === cart[0]?.suppliers[0]?.id) {
				addToCartCheck(
					product,
					quantity,
					token,
					setLoading,
					dispatch,
					cart
				);
			} else {
				setAcseptModal({
					item: product,
					open: true,
				});
			}
		}
	};

	let mount = true;

	const richProduct = product.data ? product.data : null;

	useEffect(() => {
		if (richProduct) {
			const script = document.createElement("script");
			script.setAttribute("type", "application/ld+json");
			script.async = true;
			script.textContent = `{
				"@context": "https://schema.org/",
				"@type": "Product",
				"name": "${richProduct.variation.name}",
				"image": [
					"${getImageUrl(richProduct.variation.images[0], "300x_")}",
					"${getImageUrl(richProduct.variation.images[0], "800x_")}",
					"${getImageUrl(richProduct.variation.images[0], "original")}",
				],
				"description": "${richProduct.variation.shortDescription}",
				"sku": "${richProduct.variation.sku}",
				"mpn": "${richProduct.variation.barcode}",
				"brand": {
					"@type": "Brand",
					"name": "${product.brandAttribute.property_value || ""}"
				},
				"review": {
					"@type": "Review",
					"reviewRating": {
						"@type": "Rating",
						"ratingValue": "4",
						"bestRating": "5"
					},
					"author": {
						"@type": "Person",
						"name": "${richProduct.variation.supplier}"
					}
				},
				"aggregateRating": {
					"@type": "AggregateRating",
					"ratingValue": "4.4",
					"reviewCount": "89"
				},
				"offers": {
					"@type": "Offer",
					"url": "https://${window.location.hostname}/product/${
				richProduct.variation.id
			}",
					"priceCurrency": "${richProduct.stock.sellPrice.order.find(
						(c) => c === richProduct.stock.sellPrice.first
					)}",
					"price": "${richProduct.stock.sellPrice[richProduct.stock.sellPrice.first]}"
					"priceValidUntil": "${new Date().getFullYear() + "-12-31"}",
					"itemCondition": "https://schema.org/UsedCondition",
					"availability": "https://schema.org/InStock"
				}
			}`;
			document.head.appendChild(script);
		}
	}, [richProduct]);

	useEffect(() => {
		productGallery();
		setLoadingProduct(true);
		getVariation(productId)
			.then(async (variation) => {
				if (!variation.data) throw new Error("Variation data is null");
				if (mount) {
					// await getProductsPromotions([
					// 	variation.data.variation.id
					// ]).then(([promotion]) => {
					// 	if (promotion) {
					// 		setProduct({
					// 			...variation,
					// 			data: { ...variation.data, ...promotion }
					// 		});
					// 	} else setProduct(variation);
					// });
					setProduct(variation);
					setSupplier(variation.suppliers[0]);
					setValue(variation?.suppliers[0]?.phone);
					getProductPromotion(variation.data.stock.id).then((res) => {
						setPromotion(
							res.sort((a, b) => a.originalId - b.originalId)
						);
					});
					getProductCategoryPath(variation.product.id).then((res) => {
						if (mount) {
							setCrumb(
								res.map((i) => {
									return {
										name: i.name,
										link: `/category/${i.slug}`,
									};
								})
							);
						}
					});

					setLoadingProduct(false);
					setLoading(false);
				}
			})
			.catch((err) => {
				history.push(process.env.PUBLIC_URL + "/pages/404");
				console.error(err);
			});
	}, [productId]);

	useEffect(() => {
		if (BOC && location.address) {
			setModalBOC({
				product,
				quantity,
				open: true,
			});
		}
	}, [BOC, location.address]);

	function openMapAndBOC() {
		dispatch(openModal("map"));
		setBOC(true);
		return;
	}

	function onComplated(variationId) {
		onComplatedChoosingProp(variationId, history, productId);
	}
	useEffect(() => {
		if (market.config.home === 6 || market.config.home === 1) {
			document.querySelector("body").classList.add("no-header");
		}

		return () => {
			document.querySelector("body").classList.remove("no-header");
			mount = false;
		};
	}, []);

	if (loading) {
		return <ProductPageSkleton left={"col-md-3"} right={"col-md-9"} />;
	}
	const characteristics = Object.entries(product.characteristics);

	return (
		<div className="product-page-four">
			<Helmet>
				<title>
					{settings.seoTemplateTitle
						? settings.seoTemplateTitle
								.replace(
									`[title]`,
									`${product.data.variation.name}`
								)
								.replace(
									`[region]`,
									`${address ? address : `Вашем городе`}`
								)
						: product.data.variation.name}
					| {window.location.host}
				</title>
				<meta
					name="description"
					content={
						settings.seoTemplateDescription
							? settings.seoTemplateDescription.replace(
									`[title]`,
									`${product.data.variation.name}`
							  )
							: product.product.description
					}
				/>
			</Helmet>
			<h1 className="d-none">
				{product.product.name} | {props.title}
			</h1>
			<div className="main product-page">
				{market.config.home === 6 ? (
					<div>
						<div className="back-button-div only-mobile">
							<BackButton />
						</div>
						<div className="only-desktop">
							<Breadcrumb
								breadcrumb={crumb}
								productId={productId}
							/>
						</div>
					</div>
				) : (
					<Breadcrumb breadcrumb={crumb} productId={productId} />
				)}

				<div className="page-content">
					<div className="container">
						<div className="product-details-top">
							<div className="row">
								<div className="col-md-3 col-12">
									<MediaOne
										loading={loadingProduct}
										adClass={"product-gallery-gorizontal"}
										product={product}
									/>
								</div>

								<div className="col-md-6 col-12">
									<ProductDetailTwo
										promotion={promotion}
										id={productId}
										product={product}
										onComplated={onComplated}
										selectVar={selectVar}
										name={product.product.name}
										quantity={quantity}
										setQuantity={setQuantity}
										enable={enable}
										setEnable={setEnable}
									/>
								</div>
								<div className="col-md-3">
									<div className="order-side">
										<button
											disabled={!enable}
											className={
												"btn-product buy-one-click pl-0 pr-0" +
												(enable ? "" : " disabled")
											}
											onClick={() => {
												location.address
													? setModalBOC({
															product,
															quantity,
															open: true,
													  })
													: openMapAndBOC();
											}}
											title={t("buy-one-click")}
										>
											<span>{t("buy-one-click")}</span>
										</button>
										{isIEBrowser() || isEdgeBrowser() ? (
											<button
												className="btn"
												onClick={
													market.config.home === 4
														? addToCartSingleSupplier
														: addToCartHandler
												}
												style={{ minHeight: "4rem" }}
											>
												<img
													src="/assets/images/vibo/Group1574.svg"
													alt="cart"
												/>
												{loading ? (
													<Loader />
												) : (
													<span>{t("add-cart")}</span>
												)}
											</button>
										) : (
											<button
												className="btn"
												disabled={!enable}
												onClick={
													market.config.home === 4
														? addToCartSingleSupplier
														: addToCartHandler
												}
											>
												<img
													src="/assets/images/vibo/Group1574.svg"
													alt="cart"
												/>
												{loading ? (
													<Loader />
												) : (
													<span>{t("add-cart")}</span>
												)}
											</button>
										)}
									</div>
									<div className="info-comp">
										<img
											className="vsupplier"
											src="/assets/images/vibo/image2.svg"
											alt="vsupplier"
										/>
										<Link
											to={`/supplier/${product?.product?.supplier}`}
										>
											<h6>
												<b>{supplier?.name}</b>
											</h6>
										</Link>
										<div className="flag-time">
											<div className="flag">
												{value ? (
													<PhoneInput
														value={value}
														onChange={() => {}}
													/>
												) : null}
												{supplier &&
												supplier.country ? (
													<p>{supplier.country}</p>
												) : null}
											</div>
											{supplier.creationYear ? (
												<p className="year">
													{calculateYear(
														supplier.creationYear
													)}
												</p>
											) : null}
										</div>

										<div className="img-content">
											<p>
												<img
													src="/assets/images/vibo/Vector3.svg"
													alt="img"
												/>
												{t("customization")}
											</p>
											<p>
												<img
													src="/assets/images/vibo/Vector3.svg"
													alt="img"
												/>
												{t("check_product")}
											</p>
										</div>
										<Link
											className="supplier-btn btn"
											to={`/supplier/${product?.product?.supplier}`}
										>
											<span>{t("to-shop")}</span>
										</Link>
									</div>
								</div>
							</div>
						</div>

						{product.product.id ? (
							<LikeProductsSupplier
								supplierId={product.data.stock.supplier}
							/>
						) : null}
					</div>

					<div className="descone-tabs bg-light">
						<div className="container d-flex align-items-center ">
							{product.characteristic ||
							product.product.description ? (
								<span className="p-3">
									{t("product_information")}
								</span>
							) : null}
							<Link
								className="p-3"
								to={`/supplier/${product?.product?.supplier}`}
							>
								{t("info_profile_company")}
							</Link>
						</div>
					</div>
					{product.product.crossSellTags ? (
						<CrossSellProducts product={product} />
					) : null}
					<div className="container">
						{product.characteristic?.lenght ||
						product.product.description ? (
							<div className="row descone">
								<div className="col-12">
									<DescOne
										key={productId}
										id={productId}
										product={product}
									/>
								</div>
							</div>
						) : null}
						<div className="row d-none mt-3">
							<div className="col-12  about-product">
								{characteristics.length ? (
									<>
										<p
											className="title d-flex align-items-center justify-content-between"
											onClick={() =>
												setIsOpen({ open: true })
											}
										>
											{t("characteristics")}
											<img
												src="/assets/images/vibo/rightArrow.svg"
												alt="arrow-img"
											/>
										</p>
										<div className="product-desc-content">
											<div>
												{characteristics.map(
													([
														propIndex,
														characteristic,
													]) => {
														return (
															<div
																className="charac"
																key={propIndex}
															>
																<span>
																	{dt(
																		characteristic.name
																	)}
																	&nbsp;:&nbsp;
																	{dt(
																		characteristic.value
																	)}
																</span>
															</div>
														);
													}
												)}
											</div>
										</div>
									</>
								) : null}
							</div>
							<div className="col-12">
								<button
									disabled={!enable}
									className={
										"btn-product buy-one-click pl-0 pr-0" +
										(enable ? "" : " disabled")
									}
									onClick={() => {
										location.address
											? setModalBOC({
													product,
													quantity,
													open: true,
											  })
											: openMapAndBOC();
									}}
									title={t("buy-one-click")}
								>
									<span>{t("buy-one-click")}</span>
								</button>
							</div>
						</div>
					</div>
				</div>
			</div>
			<ProductCharacteristic
				isOpen={isOpen}
				setIsOpen={setIsOpen}
				characteristics={characteristics}
			/>
			{modalBOC.open && (
				<BuyOneClick setModal={setModalBOC} modal={modalBOC} />
			)}
			{acseptModal.open ? (
				<AcseptModal
					title="Savatni tozalaysizmi?"
					isClicked={isClicked}
					acseptModal={acseptModal}
					setAcseptModal={setAcseptModal}
				/>
			) : null}
		</div>
	);
}

const mapStateToProps = (state) => {
	return {
		title: state.marketData.title,
	};
};

export default connect(mapStateToProps)(ProductPageFour);
