import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { Helmet } from "react-helmet";
import { connect, useSelector } from "react-redux";
import MediaOne from "./partials/media/media-one";
import RelatedProducts from "./partials/related-products";
import ProductDetailOne from "./partials/details/sticky-detail";
import { productGallery } from "../../utils/index";
import {
	// getProductsPromotions,
	getVariation,
} from "../../api/products";
import ProductPageSkleton from "../../components/features/loaders/ProductPageSkleton";
import { onComplatedChoosingProp } from "./helpers";
import market from "../../config/market";
import BackButton from "../../components/common/BackButton";
import SearchFormTwo from "../../components/common/partials/SearchFormTwo/";
import { dt } from "../../i18n";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { productGalerySlider } from "../../components/options";
import DescOne from "./partials/description/desc-one";
import Breadcrumb from "../../components/common/breadcrumb";
import { getImageUrl } from "../../utils/oxMethods";
import CrossSellProducts from "./CrossSellProducts";

function ProductPageSix(props) {
	const productId = props.match.params.id;
	const history = useHistory();
	const [isInputClicked, setIsInputClicked] = useState(false);
	const [product, setProduct] = useState({ product: { id: -1 } });
	const [loading, setLoading] = useState(true);
	const [loadingProduct, setLoadingProduct] = useState(false);
	const settings = useSelector((state) => state.marketData.settings);
	const address = useSelector((state) =>
		state.user.location?.address?.administrativeArea
			? state.user.location.address.administrativeArea
			: state.user.location?.address?.name
	);
	let mount = true;
	function toTop() {
		window.scroll({
			top: 0,
		});
	}

	const richProduct = product.data ? product.data : null;

	useEffect(() => {
		if (richProduct) {
			const script = document.createElement("script");
			script.setAttribute("type", "application/ld+json");
			script.async = true;
			script.textContent = `{
				"@context": "https://schema.org/",
				"@type": "Product",
				"name": "${richProduct.variation.name}",
				"image": [
					"${getImageUrl(richProduct.variation.images[0], "300x_")}",
					"${getImageUrl(richProduct.variation.images[0], "800x_")}",
					"${getImageUrl(richProduct.variation.images[0], "original")}",
				],
				"description": "${richProduct.variation.shortDescription}",
				"sku": "${richProduct.variation.sku}",
				"mpn": "${richProduct.variation.barcode}",
				"brand": {
					"@type": "Brand",
					"name": "${product.brandAttribute?.property_value}"
				},
				"review": {
					"@type": "Review",
					"reviewRating": {
						"@type": "Rating",
						"ratingValue": "4",
						"bestRating": "5"
					},
					"author": {
						"@type": "Person",
						"name": "${richProduct.variation.supplier}"
					}
				},
				"aggregateRating": {
					"@type": "AggregateRating",
					"ratingValue": "4.4",
					"reviewCount": "89"
				},
				"offers": {
					"@type": "Offer",
					"url": "https://${window.location.hostname}/product/${
				richProduct.variation.id
			}",
					"priceCurrency": "${richProduct.stock.sellPrice.order.find(
						(c) => c === richProduct.stock.sellPrice.first
					)}",
					"price": "${richProduct.stock.sellPrice[richProduct.stock.sellPrice.first]}"
					"priceValidUntil": "${new Date().getFullYear() + "-12-31"}",
					"itemCondition": "https://schema.org/UsedCondition",
					"availability": "https://schema.org/InStock"
				}
			}`;
			document.head.appendChild(script);
		}
	}, [richProduct]);

	useEffect(() => {
		productGallery();
		setLoadingProduct(true);
		getVariation(productId)
			.then(async (variation) => {
				if (!variation.data) throw new Error("Variation data is null");
				if (mount) {
					// await getProductsPromotions([
					// 	variation.data.variation.id
					// ]).then(([promotion]) => {
					// 	if (promotion) {
					// 		setProduct({
					// 			...variation,
					// 			data: { ...variation.data, ...promotion }
					// 		});
					// 	} else setProduct(variation);
					// });
					setProduct(variation);
					setLoading(false);
					setLoadingProduct(false);
				}
			})
			.catch((err) => {
				history.push(process.env.PUBLIC_URL + "/pages/404");
				console.error(err);
			});
		toTop();
	}, [productId]);

	useEffect(() => {
		if (
			market.config.home === 6 ||
			market.config.home === 1 ||
			market.config.home === 9
		) {
			document.querySelector("body").classList.add("no-header");
		}
		document.querySelector("body").classList.add(`product-page-six`);
		return () => {
			mount = false;
			document.querySelector("body").classList.remove("no-header");
			document.querySelector("body").classList.remove(`product-page-six`);
		};
	}, []);

	function onComplated(variationId) {
		onComplatedChoosingProp(variationId, history, productId);
	}

	let placeHolderImage = [
		{
			id: 1,
			urls: {
				"50x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
				"100x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
				"150x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
				"300x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
				"800x_": "https://telegra.ph/file/3a01e53662cea1a425220.png",
				original: "https://telegra.ph/file/3a01e53662cea1a425220.png",
			},
		},
	];
	const bigImages =
		product?.data?.variation?.images?.length === 0
			? placeHolderImage
			: product?.data?.variation?.images;
	if (loading) {
		return <ProductPageSkleton left={"col-md-3"} right={"col-md-9"} />;
	}
	// console.log(product.data.variation.stocks);
	// console.log(
	// 	product.data.variation.productProperties.filter(
	// 		(product) => product.value.length > 0
	// 	)
	// );
	return (
		<>
			<Helmet>
				<title>
					{settings.seoTemplateTitle
						? settings.seoTemplateTitle
								.replace(
									`[title]`,
									`${product.data.variation.name}`
								)
								.replace(
									`[region]`,
									`${address ? address : `Вашем городе`}`
								)
						: product.data.variation.name}
				</title>
				<meta
					name="description"
					content={
						settings.seoTemplateDescription
							? settings.seoTemplateDescription.replace(
									`[title]`,
									`${product.data.variation.name}`
							  )
							: product.product.description
					}
				/>
			</Helmet>
			<div className="product-page-six">
				<div className="category-header">
					<div
						className={
							isInputClicked
								? "d-none"
								: "d-flex align-items-center"
						}
					>
						<BackButton />
						<h2 className="category-title only-mobile">
							{dt(product.product.name)}
						</h2>
					</div>
					<div className="row searcher-row w-100">
						<div className="col-12 only-mobile d-none">
							<SearchFormTwo
								isInputClicked={isInputClicked}
								setIsInputClicked={setIsInputClicked}
							/>
						</div>
					</div>
					<div className="bread-crumb">
						<Breadcrumb />
					</div>
				</div>
				<div className="page-content">
					<Splide
						className="splide only-mobile"
						options={productGalerySlider}
					>
						{bigImages.map((item, index) => (
							<SplideSlide key={index}>
								<div
									className="big-img-with-view-button"
									key={index}
								>
									<img
										src={item.urls["800x_"]}
										alt="product back"
									/>
								</div>
							</SplideSlide>
						))}
					</Splide>

					<div className="container">
						{/* {console.log(isEmpty)} */}
						<div className="product-details-top ">
							<div className="row skel-pro-single sticky loaded">
								<div className="col-12">
									<div className="row">
										<div
											className={`col-lg-6 col-md-0 col-12`}
										>
											<MediaOne
												adClass={
													"product-gallery-gorizontal only-desktop"
												}
												loading={loadingProduct}
												id={productId}
												product={product}
											/>
										</div>

										<div className="col-lg-6 col-md-12 pl-md-5">
											<ProductDetailOne
												id={productId}
												product={product}
												onComplated={onComplated}
												name={product.product.name}
											/>
										</div>
									</div>
									<div className="col-12 product-info px-0">
										<DescOne product={product} />
									</div>
								</div>
							</div>
						</div>
					</div>
					{product.product.crossSellTags ? (
						<CrossSellProducts product={product} />
					) : null}
					<div className="container">
						<RelatedProducts product={product} />
					</div>
				</div>
			</div>
		</>
	);
}

const mapStateToProps = (state) => {
	return {
		title: state.marketData.title,
	};
};

export default connect(mapStateToProps)(ProductPageSix);
