import React, { useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { isIEBrowser, isEdgeBrowser } from "../../../../utils";
import { formatterCurrency } from "../../../../utils/string";
import PropertiesView from "../media/components/PropertiesView";
import QueryString from "qs";
import BuyOneClick from "../../../../components/features/modal/BuyOneClick";
import InputChangerCount from "../../../../components/common/InputChangerCount";
import ProductBrandAttrAndSupplier from "./components/ProductBrandAttrAndSupplier";
import { useTranslation } from "react-i18next";
import { setChoosenProduct } from "../../../../actions/choosenProducts";
import addToCartCheck from "../../../../components/features/product/addToCartCheck";
import Loader from "../../../../components/features/loaders/loader";
import SizeTableModal from "../../../../components/features/modal/SizeTableModal";
import { openModal } from "../../../../actions/modal";

function ProductDetailOne(props) {
	const { t } = useTranslation();
	const dispatch = useDispatch();
	const { cart } = useSelector(state => state.cartlist);
	const location = useSelector(state => state.user.location);
	const { product, onComplated, name, selectVar, token } = props;
	const [quantity, setQuantity] = useState(1);
	const [enable, setEnable] = useState(selectVar);
	const [modalBOC, setModalBOC] = useState({ open: false });
	const [loading, setLoading] = useState(false);
	const [BOC, setBOC] = useState(false);
	const [modal, setModal] = useState({ open: false });

	useEffect(() => {
		if (loading) {
			setEnable(true);
		}
	}, [loading]);

	const addToCartHandler = () => {
		addToCartCheck(product, quantity, token, setLoading, dispatch, cart);
	};
	const { productList } = useSelector(state => state.choosenProducts);
	const haveChoosen = productList.find(
		p => p.stock.id === product.data.stock.id
	);
	const max_count = parseInt(product.data.count);

	function onChange(count) {
		if (parseInt(count) > max_count) {
			toast.error(t("amount-balance"), {
				position: "bottom-left"
			});
		} else {
			setQuantity(parseInt(count));
		}
	}

	function onComplatedMiddle(variationId) {
		if (variationId) {
			setEnable(true);
			onComplated(variationId);
		} else {
			setEnable(false);
		}
	}

	function openMapAndBOC() {
		setBOC(true);
		dispatch(openModal("map"));
	}

	useEffect(() => {
		if (BOC && location.address) {
			setModalBOC({
				product,
				quantity,
				open: true
			});
		}
	}, [BOC, location.address]);

	useEffect(() => {
		setQuantity(1);
	}, [product]);

	return (
		<div className="product-details">
			<h1 className="product-title">{product.product.name}</h1>
			{0 < product.data.discount ? (
				<div className="product-price">
					<span className="new-price">
						{formatterCurrency(product.data.newPrice)}
					</span>
					<span className="old-price">
						{formatterCurrency(product.data.oldPrice)}
					</span>
				</div>
			) : (
				<div className="product-price">
					{formatterCurrency(product.data.newPrice)}
				</div>
			)}

			<div className="product-content">
				<h6>
					{t("articul")}: {product.data.variation.sku}
				</h6>
			</div>

			{product.data.variation.properties ? (
				<PropertiesView
					product={product}
					onComplated={onComplatedMiddle}
					selectVar={selectVar}
				/>
			) : null}

			{enable ? (
				<div className="details-filter-row details-row-size">
					<label htmlFor="qty">{t("count")}:</label>
					<div className="product-details-quantity">
						<div>
							<InputChangerCount
								initValue={quantity}
								onChange={value =>
									onChange(value, product.data.variation.id)
								}
								variationId={product.data.variation.id}
							/>
						</div>
					</div>
				</div>
			) : (
				<div className="product-details-action">
					<span className="choose-variation-text">
						{t("select-variation")}
					</span>
				</div>
			)}

			<div className="product-details-action">
				{isIEBrowser() || isEdgeBrowser() ? (
					<button
						className="btn-product btn-cart"
						onClick={addToCartHandler}
						style={{ minHeight: "4rem" }}
					>
						{loading ? <Loader /> : <span>{t("add-cart")}</span>}
					</button>
				) : (
					<button
						className="btn-product btn-cart"
						disabled={!enable}
						onClick={addToCartHandler}
					>
						{loading ? <Loader /> : <span>{t("add-cart")}</span>}
					</button>
				)}

				<div className="details-action-wrapper">
					<button
						disabled={!enable}
						className={
							"btn-product pl-0 pr-0" +
							(enable ? "" : " disabled")
						}
						onClick={() => {
							location.address
								? setModalBOC({
										product,
										quantity,
										open: true
								  })
								: openMapAndBOC();
						}}
						title={t("buy-one-click")}
					>
						<span>{t("buy-one-click")}</span>
					</button>
				</div>

				<div className="details-action-wrapper mr-5">
					<span
						onClick={() =>
							dispatch(setChoosenProduct(product.data))
						}
						style={{
							cursor: "pointer",
							fontSize: "24px",
							color: haveChoosen ? "red" : ""
						}}
					>
						<span className="favourities">{t("favourites")}</span>
						<i className="icon icon-heart"></i>
					</span>
				</div>

				<div
					className="size-table d-flex align-items-center d-none"
					onClick={() => {
						setModal({ open: true });
					}}
				>
					<span>{t("size-table")}</span>
					<img
						src="/assets/images/atlanta/tablesizes.jpg"
						alt="tablesizeicon"
						width="35"
						height="35"
					/>
				</div>
			</div>

			<div className="product-details-footer row">
				<ProductBrandAttrAndSupplier product={product} />

				<div className="col-lg-3 col-md-12 col-12 social-icons social-icons-sm">
					<span className="social-label">{t("share")}:</span>
					<a
						href={`https://telegram.me/share/url?${QueryString.stringify(
							{
								url: window.location.href,
								text: name
							}
						)}`}
						target="blank"
						className="social-icon"
						title="Telegram"
					>
						<i className="icon-telegram"></i>
					</a>
					<a
						href={`https://www.facebook.com/sharer.php?s/url?${QueryString.stringify(
							{
								url: window.location.href,
								text: name
							}
						)}`}
						target="blank"
						className="social-icon"
						title="Facebook"
					>
						<i className="icon-facebook-f"></i>
					</a>
				</div>
			</div>

			{modalBOC.open && (
				<BuyOneClick setModal={setModalBOC} modal={modalBOC} />
			)}
			{modal.open && <SizeTableModal modal={modal} setModal={setModal} />}
		</div>
	);
}

export default connect(state => ({
	settings: state.marketData.settings,
	token: state.user.tokenData?.token
}))(ProductDetailOne);
