import React from "react";
import market from "../../config/market";
import DefaultProduct from "./DefaultProduct";
import ProductThree from "./ProductThree";
import StickyProduct from "./StickyProduct";
import ProductPageFour from "./ProductPageFour";
import ProductPageFive from "./ProductPageFive";
import ProductPageSix from "./ProductPageSix";
import ProductPageSeven from "./ProductPageSeven";
import ProductPageEight from "./ProductPageEight";

export default function ProductPage(props) {
	switch (market.config.productPage) {
		case 1:
			return <DefaultProduct {...props} />;
		case 2:
			return <StickyProduct {...props} />;
		case 3:
			return <ProductThree {...props} />;
		case 4:
			return <ProductPageFour {...props} />;
		case 5:
			return <ProductPageFive {...props} />;
		case 6:
			return <ProductPageSix {...props} />;
		case 7:
			return <ProductPageSeven {...props} />;
		case 8:
			return <ProductPageEight {...props} />;
		default:
			return <DefaultProduct {...props} />;
	}
}
