import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import QueryString from "qs";
import { useDispatch, useSelector } from "react-redux";
import { setChoosenProduct } from "../../../../../actions/choosenProducts";
import market from "../../../../../config/market";

const ProductNavbar = ({ product }) => {
	const dispatch = useDispatch();
	const [shareMenu, setShareMenu] = useState("");
	const categoryPath = useSelector(state => state.any.categoryPath);
	const { productList } = useSelector(state => state.choosenProducts);
	const haveChoosen = productList.find(
		p => p.stock.id === product.data.stock.id
	);
	const h = useHistory();

	return (
		<div className="product-navbar d-flex align-items-center">
			<div
				onClick={() =>
					categoryPath
						? h.push(`/category/${categoryPath}`)
						: h.goBack()
				}
				style={{ cursor: "pointer" }}
				className="text-primary exit-product-page"
			>
				<img src={market.config.icons.backArrow} alt="back" />
			</div>
			<div className="d-flex align-items-center">
				<div
					className={`${
						haveChoosen ? `choose-product active` : `choose-product`
					}`}
					onClick={() => {
						dispatch(setChoosenProduct(product.data));
					}}
				>
					<img
						src={
							haveChoosen
								? market.config.icons.favouritesActive
								: market.config.icons.favourites
						}
						alt="icon"
					/>
				</div>

				<div className="dropdown share-dropdown">
					<div
						className="dropdown-toggle text-primary"
						style={{ cursor: "pointer" }}
						onClick={() =>
							setShareMenu(shareMenu === "active" ? "" : "active")
						}
					>
						<img src={market.config.icons.export} alt="export" />
					</div>
				</div>
				<div
					className={`dropdown-menu share-dropdown-menu ${shareMenu}`}
				>
					<div className="social-icons social-icons-sm">
						<a
							href={`https://telegram.me/share/url?${QueryString.stringify(
								{
									url: window.location.href,
									text: product?.product?.name
								}
							)}`}
							target="blank"
							className="social-icon"
							title="Telegram"
						>
							<i className="icon-telegram"></i>
							<span>Telegram</span>
						</a>
						<a
							href={`https://www.facebook.com/sharer.php?s/url?${QueryString.stringify(
								{
									url: window.location.href,
									text: product?.product?.name
								}
							)}`}
							target="blank"
							className="social-icon"
							title="Facebook"
						>
							<i className="icon-facebook-f"></i>
							<span>Facebook</span>
						</a>
					</div>
				</div>
			</div>
		</div>
	);
};

export default ProductNavbar;
