import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { isIEBrowser, isEdgeBrowser } from "../../../../utils";
import { formatterCurrency } from "../../../../utils/string";
import PropertiesViewTwo from "../media/components/PropertiesViewTwo";
import ProductBrandAttrAndSupplier from "./components/ProductBrandAttrAndSupplier";
import { useTranslation } from "react-i18next";
import addToCartCheck from "../../../../components/features/product/addToCartCheck";
import Loader from "../../../../components/features/loaders/loader";
import market from "../../../../config/market";
import { Link } from "react-router-dom";

export default function ProductDetailThree(props) {
	const { t } = useTranslation();
	const cart = useSelector(state => state.cartlist.cart);
	const { product, onComplated } = props;
	// const haveCart = cart.find(p => p.data.variation.id === parseInt(id));
	const { token } = useSelector(state => state.user.tokenData);
	const [enable, setEnable] = useState(true);
	const [disablebtn, setDisablebtn] = useState(false);
	const [productCount] = useState(1);
	const [loading, setLoading] = useState(false);
	const dispatch = useDispatch();
	const { cartDescription } = useSelector(state => state.marketData.settings);

	useEffect(() => {
		if (loading) {
			setDisablebtn(true);
		} else setDisablebtn(false);
	}, [loading]);

	const addToCartHandler = () => {
		addToCartCheck(
			product,
			productCount,
			token,
			setLoading,
			dispatch,
			cart
		);
	};

	function onComplatedMiddle(variationId) {
		if (variationId) {
			setEnable(true);
			onComplated(variationId);
		} else {
			setEnable(false);
		}
	}

	return (
		<>
			<div className="product-details">
				<h1 className="product-title">{product.product.name}</h1>
				{market.config.home === 5 ? (
					<div className="product-info">
						<p>
							{t("articul")}: {product.data.variation.sku}
						</p>
						<Link to={`/supplier/${product.suppliers[0].id}`}>
							{t("supplier")}: {product.suppliers[0].name}
						</Link>
						{product.brandAttribute ? (
							<p>
								{t("brand")}:
								{product.brandAttribute.property_value}
							</p>
						) : null}
					</div>
				) : null}
				{product.data.variation.properties ? (
					<PropertiesViewTwo
						product={product}
						onComplated={onComplatedMiddle}
						setEnalbe={setEnable}
					/>
				) : null}
				{market.config.home === 5 ? (
					<>
						<div
							className="delivery-and-aborting"
							dangerouslySetInnerHTML={{
								__html: cartDescription
							}}
						/>
						<div className="register">
							<div className="pay-info">
								<h6>
									Купить в рассрочку <br />
									<b>от 200 000 сум/месяц</b>
								</h6>
							</div>
							<button className="btn">{t("confirom")}</button>
						</div>
					</>
				) : null}
				<div className="product-details-footer row">
					<ProductBrandAttrAndSupplier product={product} />
				</div>
			</div>
			<div className="product-details-action">
				{/* {haveCart ? (
					<div className="product-count-change w-100">
						<Link
							to={`${process.env.PUBLIC_URL}${
								token ? "/checkout" : "/cart"
							}`}
							className="btn-product btn-cart align-items-center mx-auto"
						>
							<span>
								{!enable
									? t("choose-variation")
									: t("checkout-order")}
							</span>
							<span
								className="new-price"
								style={{
									marginBottom: "5px",
									fontSize: "12px"
								}}
							>
								{formatterCurrency(product.data.newPrice)}
							</span>
						</Link>
					</div>
				) : (
					<> */}
				<div className="product-price">
					<span className=" text-dark">
						{formatterCurrency(product.data.newPrice)}
					</span>
					{0 < product.data.discount ? (
						<span className="old-price">
							{formatterCurrency(product.data.oldPrice)}
						</span>
					) : null}
				</div>
				{isIEBrowser() || isEdgeBrowser() ? (
					<button
						className="btn-product btn-cart"
						onClick={addToCartHandler}
						style={{ minHeight: "4rem" }}
					>
						{loading ? <Loader /> : <span>{t("add-cart")}</span>}
					</button>
				) : (
					<button
						className="btn-product btn-cart"
						disabled={!enable || disablebtn}
						onClick={addToCartHandler}
					>
						{loading ? (
							<Loader />
						) : (
							<span>
								{!enable
									? t("choose-variation")
									: t("add-cart")}
							</span>
						)}
					</button>
				)}
				{/* </>
				)} */}
			</div>
		</>
	);
}
