import React, { useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { isIEBrowser, isEdgeBrowser } from "../../../../utils";
import PropertiesView from "../media/components/PropertiesView";
import BuyOneClick from "../../../../components/features/modal/BuyOneClick";
import InputChangerCount from "../../../../components/common/InputChangerCount";
import { useTranslation } from "react-i18next";
import addToCartCheck from "../../../../components/features/product/addToCartCheck";
import Loader from "../../../../components/features/loaders/loader";
import DefenceModal from "../../../../components/features/modal/DefenceModal";
import VariationsModal from "../../../../components/features/modal/VariationsModal";
import { formatterCurrency } from "../../../../utils/string";
import GetPromotions from "./GetPromotions";
import { getPrice } from "../../../../utils/oxMethods";

function getOfferedPrice(promotion, oldPriceValue) {
	return oldPriceValue - getPrice(promotion.offers[0].config.fixedDiscount);
}

function checkIsNumberInnerRange({ min, max }, count) {
	return min <= count && max >= count;
}

function getVariationFieldTypeRule(promotion) {
	return promotion.rules.find(r => r.type === "variation_field");
}

function getPromotionByCount(promotions, count) {
	return promotions.find(promotion =>
		checkIsNumberInnerRange(
			getVariationFieldTypeRule(promotion).config.total_count,
			count
		)
	);
}

function ProductDetailTwo(props) {
	const { t } = useTranslation();
	const dispatch = useDispatch();
	const { cart } = useSelector(state => state.cartlist);
	const [openModal, setOpenModal] = useState({ open: false });
	const [openModalTwo, setOpenModalTwo] = useState({ open: false });

	const {
		promotion,
		product,
		onComplated,
		selectVar,
		token,
		quantity,
		setQuantity,
		enable,
		setEnable
	} = props;

	const [modalBOC, setModalBOC] = useState({ open: false });
	const [loading, setLoading] = useState(false);
	const [minCount, setMinCount] = useState(product?.product?.minOrder);
	const max_count = parseInt(product.data.count);

	useEffect(() => {
		if (loading) {
			setEnable(true);
		}
	}, [loading]);

	const addToCartHandler = () => {
		addToCartCheck(product, quantity, token, setLoading, dispatch, cart);
		setOpenModalTwo({ open: false });
	};

	function onChange(count) {
		if (parseInt(count) > max_count) {
			toast.error(t("amount-balance"), {
				position: "bottom-left"
			});
		} else {
			setQuantity(parseInt(count < minCount ? minCount : count));
		}
	}

	function onComplatedMiddle(variationId) {
		if (variationId) {
			setEnable(true);
			onComplated(variationId);
		} else {
			setEnable(false);
		}
	}

	useEffect(() => {
		setQuantity(minCount || 1);
	}, [product, minCount]);

	return (
		<div className={"product-details"}>
			<div>
				<h1 className="product-title">{product.product.name}</h1>
				<div className="row info-table">
					<GetPromotions
						product={product}
						promotion={promotion}
						setMinCount={setMinCount}
					/>
				</div>
			</div>

			<div className="row defence">
				<div className="col-12 d-flex ">
					<p
						className="text-defence d-flex justify-content-between"
						onClick={() => {
							setOpenModal({
								open: true
							});
						}}
					>
						<span>{t("protection")}</span>
						<img
							src="/assets/images/vibo/rightArrow.svg"
							alt="arrow-img"
						/>
					</p>
					<div className="content">
						<p>
							<img src="/assets/images/vibo/Vector.png" alt="" />
							<span>
								<b>{t("guarantee")}</b>
							</span>
						</p>
						<p>
							<img src="/assets/images/vibo/Vector.png" alt="" />
							<span>
								<b>{t("return-politics")}</b>
							</span>
						</p>
					</div>
				</div>
			</div>

			{product.data.variation.properties ? (
				<PropertiesView
					product={product}
					onComplated={onComplatedMiddle}
					selectVar={selectVar}
				/>
			) : null}

			<div className="row info-content">
				<div className="col-12 d-none">
					<p
						className="title d-flex align-items-center justify-content-between"
						onClick={() => setOpenModalTwo({ open: true })}
					>
						<span>{t("variety")}</span>
						<img
							src="/assets/images/vibo/rightArrow.svg"
							alt="arrow-img"
						/>
					</p>
					<div className="color-size-add-cart">
						<div className="d-flex align-items-center">
							{product.data.variation.properties.map(
								(prop, i) => {
									return (
										<p className="mr-2" key={i}>
											{prop.name + ": " + prop.value}
										</p>
									);
								}
							)}
						</div>
						<div className="cart-button-div d-none">
							{isIEBrowser() || isEdgeBrowser() ? (
								<button
									className="btn-product btn-cart"
									onClick={addToCartHandler}
									style={{ minHeight: "4rem" }}
								>
									{loading ? (
										<Loader />
									) : (
										<span>{t("add-cart")}</span>
									)}
								</button>
							) : (
								<button
									className="btn-product btn-cart"
									disabled={!enable}
									onClick={addToCartHandler}
								>
									{loading ? (
										<Loader />
									) : (
										<span>{t("add-cart")}</span>
									)}
								</button>
							)}
						</div>
					</div>
				</div>
				<div className="col-12 full-power">
					<div className="d-flex align-items-center">
						{enable ? (
							<div className="details-filter-row details-row-size">
								<div className="product-details-quantity">
									<div>
										<InputChangerCount
											initValue={quantity}
											min={minCount}
											disable={false}
											onChange={value =>
												onChange(
													value,
													product.data.variation.id
												)
											}
											variationId={
												product.data.variation.id
											}
										/>
									</div>
								</div>
							</div>
						) : (
							<div className="product-details-action">
								<span className="choose-variation-text">
									{t("select-variation")}
								</span>
							</div>
						)}
					</div>
				</div>
			</div>
			<ViewTotalPrice
				quantity={quantity}
				promotions={promotion}
				oldPriceValue={product?.data?.oldPrice}
			/>

			<DefenceModal openModal={openModal} setOpenModal={setOpenModal} />
			<VariationsModal
				product={product}
				promotion={promotion}
				openModalTwo={openModalTwo}
				setOpenModalTwo={setOpenModalTwo}
				quantity={quantity}
				setQuantity={setQuantity}
				onChange={onChange}
				enable={enable}
				addToCartHandler={addToCartHandler}
				onComplatedMiddle={onComplatedMiddle}
				selectVar={selectVar}
			/>
			<BuyOneClick setModal={setModalBOC} modal={modalBOC} />
		</div>
	);
}

function checkPromotion(promotions) {
	return promotions.find(promotion =>
		promotion.rules.find(rule => rule.type === "variation_field")
	);
}

const ViewTotalPrice = React.memo(({ quantity, oldPriceValue, promotions }) => {
	const { t } = useTranslation();
	const promotion =
		promotions?.length &&
		checkPromotion(promotions) &&
		getPromotionByCount(promotions, quantity);
	const price = promotion
		? getOfferedPrice(promotion, oldPriceValue)
		: oldPriceValue;

	return (
		<div className="row info_result">
			<div className="col-12">
				<span className="mr-5">
					{quantity || 1} {t("pcs")}
				</span>
				<b>{formatterCurrency(price * (quantity || 1))}</b>
			</div>
			<div className="col-12">
				<span className="mr-5 ">{t("total")}</span>
				<b>{formatterCurrency(price * (quantity || 1))}</b>
			</div>
		</div>
	);
});

export default connect(state => ({
	settings: state.marketData.settings,
	token: state.user.tokenData?.token
}))(ProductDetailTwo);
