import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import Accordion from "./accordion/accordion";
import Card from "./accordion/card";

function DescTwo({ product }) {
	const { t } = useTranslation();
	const [height, setHeight] = useState(false);
	const [heightChar, setHeightChar] = useState(false);
	const { settings } = useSelector(state => state.marketData);
	const characteristics = Object.entries(product.characteristics);

	return (
		<>
			<div className="product-details-tab">
				{product.product.description ? (
					<div>
						<span className="nav-link">{t("description")}</span>
						<div
							style={{
								height: height ? "auto" : "60px",
								overflow: "hidden"
							}}
							className="product-desc-content"
							dangerouslySetInnerHTML={{
								__html: product.product.description
							}}
						></div>

						{product.product.description.split(" ").length > 15 ? (
							<p
								onClick={() => setHeight(!height)}
								style={{
									color: "#8E8E93",
									cursor: "pointer",
									marginTop: "7px"
								}}
							>
								{!height ? `${t("more")}` : ``}
							</p>
						) : null}
					</div>
				) : null}
				{characteristics.length ? (
					<div>
						<span className="nav-link">{t("characteristics")}</span>

						<div
							className="product-desc-content"
							style={{
								height: height ? "auto" : "80px",
								overflow: "hidden"
							}}
						>
							<table
								className="table"
								style={{ marginBottom: "0" }}
							>
								<tbody>
									{characteristics?.map(
										([propIndex, characteristic]) => {
											return (
												settings.productProperty &&
												Object.entries(
													settings?.productProperty
												)?.find(
													p =>
														p[1] ===
														characteristic.name
												) && (
													<tr
														className="tr"
														key={propIndex}
													>
														<td
															style={{
																paddingTop:
																	"1rem",
																paddingBottom:
																	"1rem",
																width: "20%"
															}}
														>
															{
																characteristic.name
															}
														</td>
														<td
															style={{
																paddingTop:
																	"1rem",
																paddingBottom:
																	"1rem"
															}}
														>
															{
																characteristic.value
															}
														</td>
													</tr>
												)
											);
										}
									)}
								</tbody>
							</table>
						</div>
						{characteristics.length > 3 ? (
							<p
								onClick={() => setHeightChar(!heightChar)}
								style={{
									color: "#8E8E93",
									cursor: "pointer"
								}}
							>
								{!heightChar ? `${t("more")}` : ``}
							</p>
						) : null}
					</div>
				) : null}

				<Accordion adClass="accordion-plus product-details-accordion">
					<Card
						title={t("delivery-return")}
						adClass="card-box card-sm"
					>
						<div className="product-desc-content">
							<div>
								<h3>Курьерская доставка по г. Ташкент</h3>
								<p>
									Вы можете заказать доставку курьером,
									который доставит заказ по указанному адресу
									в будние дни и субботу с 10.00 до 18.00.
									Наша курьерская служба, после поступления
									заявки, свяжется с вами и предложит выбрать
									удобное время доставки. При необходимости,
									уточнит адрес и детали заявки.
								</p>
								<p>
									Вещи доставляются в зависимости от времени
									поступления заказа. Заказы, оформленные до
									обеда, доставляются в этот же день, после
									обеда — на следующий день.
								</p>
								<br />
								<h3>Возврат/Обмен</h3>
								<p>
									Возврат или обмен товара возможны только при
									соблюдении следующих условий:
								</p>
								<ul>
									<li>
										Товар не был в употреблении и не был
										использован;
									</li>
									<li>
										Сохранены его товарный вид,
										потребительские свойства, пломбы,
										фабричные ярлыки;
									</li>
									<li>
										Имеется документ, подтверждающий факт
										приобретения товара (чек, товарный чек).
									</li>
								</ul>
								<p>
									Внешний вид и комплектность товара должны
									быть проверены в момент доставки товара.
									После получения товара претензии к внешним
									дефектам товара, его количеству,
									комплектности и товарному виду не
									принимаются.
								</p>
							</div>
						</div>
					</Card>
				</Accordion>
			</div>
		</>
	);
}

export default DescTwo;
