import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { dt } from "../../../../i18n";

function DescThree({ product }) {
	const { t } = useTranslation();
	const settings = useSelector(state => state.marketData.settings);
	const characteristics = Object.entries(product.characteristics);
	const [height, setHeight] = useState(false);
	const [heightChar, setHeightChar] = useState(false);
	// var width = window.innerWidth;

	return (
		<div className="row w-100 ">
			<div className="description-and-characterisics">
				{product.product.description ? (
					<div className="col-md-6 col-12">
						{height ? null : <div className="gradient-div" />}

						<div
							className="description"
							style={
								window.innerWidth < 961
									? {
											height: height ? "auto" : "130px",
											overflow: "hidden"
									  }
									: null
							}
						>
							<h3>{t("description")}</h3>
							<div
								className="product-desc-content"
								dangerouslySetInnerHTML={{
									__html: product.product.description
								}}
							></div>
						</div>
						{window.innerWidth < 961 &&
						product.product.description.split(" ").length > 15 ? (
							<span
								className="show-all"
								onClick={() => setHeight(!height)}
							>
								{!height ? `${t("Свернуть описание")}` : ``}
							</span>
						) : null}
					</div>
				) : null}
				{characteristics && characteristics.length ? (
					<div className="col-md-6 col-12">
						{heightChar ? null : <div className="gradient-div" />}
						<div
							className="characteristics"
							style={
								window.innerWidth < 961
									? {
											height: heightChar
												? "auto"
												: "100px",
											overflow: "hidden"
									  }
									: null
							}
						>
							<h3>{t("characteristics")}</h3>
							<div className="product-desc-content">
								{characteristics &&
									characteristics.length &&
									characteristics.map(
										([propIndex, characteristic]) => {
											return (
												settings.productProperty &&
												Object.entries(
													settings.productProperty
												)?.find(
													p =>
														p[1] ===
														characteristic?.name
												) && (
													<div key={propIndex}>
														<div className="content-row">
															<p className="info-name">
																{dt(
																	characteristic?.name
																)}
															</p>
															<span className="dashed" />
															<p className="info-content">
																{dt(
																	characteristic.value
																)}
															</p>
														</div>
													</div>
												)
											);
										}
									)}
							</div>
						</div>
						{window.innerWidth < 961 &&
						characteristics.length > 2 ? (
							<span
								className="show-all"
								onClick={() => setHeightChar(!heightChar)}
							>
								{!heightChar
									? `${t("Свернуть характеристики")}`
									: ``}
							</span>
						) : null}
					</div>
				) : null}
			</div>
		</div>
	);
}

export default DescThree;
