import React, { useEffect } from "react";
import { getPrice } from "../../../../utils/oxMethods";
import { formatterCurrency } from "../../../../utils/string";
import { useTranslation } from "react-i18next";
import { dt } from "../../../../i18n";

function getRule(item) {
	return item.rules.find(r => r.type === "variation_field");
}

export default function GetPromotions({ promotion, product, setMinCount }) {
	if (!promotion || promotion?.length === 0) {
		return <h3>{formatterCurrency(product?.data?.oldPrice)}</h3>;
	}
	const { t } = useTranslation();
	useEffect(() => {
		if (setMinCount) {
			const minCount = promotion.map(
				item => getRule(item).config.total_count.min
			);
			setMinCount(Math.min(...minCount));
		}
	}, [promotion]);

	return promotion.map((item, i) => {
		let rule = getRule(item);
		let offer = item.offers.find(r => r.type === "product_fixed_discount");
		let totalCount = rule?.config?.total_count;
		let fixedDiscount = offer?.config?.fixedDiscount;

		if (!rule) {
			return (
				<h3 key={i}>{formatterCurrency(product?.data?.oldPrice)}</h3>
			);
		}

		return (
			<div key={i} className="col-md-4 col-6">
				<div className="count-price">
					<p>
						{totalCount.min}-{totalCount.max}{" "}
						{t(`${product.data.variation.unit}`)}
					</p>
					<h3>
						{formatterCurrency(
							product?.data?.oldPrice - getPrice(fixedDiscount)
						)}
					</h3>
					<del>{formatterCurrency(product?.data?.oldPrice)}</del>
				</div>
				<div className="time-day">
					<p>
						{t("time_days")}: <b>{dt(item.description)}</b>
					</p>
				</div>
			</div>
		);
	});
}
