import React, { useState } from "react";
import Modal from "react-modal";
import { getImageUrl } from "../../../utils/oxMethods";
import InputChangerCount from "../../common/InputChangerCount";
import { isIEBrowser, isEdgeBrowser } from "../../../utils";
import { useTranslation } from "react-i18next";
import Loader from "../loaders/loader";
import PropertiesView from "../../../views/product/partials/media/components/PropertiesView";
import { formatterCurrency } from "../../../utils/string";
import GetPromotions from "../../../views/product/partials/details/GetPromotions";

export default function VariationsModal(props) {
	const {
		promotion,
		openModalTwo,
		setOpenModalTwo,
		product,
		quantity,
		onChange,
		enable,
		addToCartHandler,
		onComplatedMiddle,
		selectVar
	} = props;
	const [
		loading
		// , setLoading
	] = useState(false);
	const { t } = useTranslation();

	const customStyles = {
		overlay: {
			backgroundColor: "rgba(77,77,77,0.6)",
			zIndex: "10000"
		}
	};
	const styleModalBody = {
		padding: "2rem"
	};

	const productImg = product?.data?.variation?.images[0];

	return (
		<div>
			<Modal
				isOpen={openModalTwo.open}
				onRequestClose={() => setOpenModalTwo({ open: false })}
				style={customStyles}
				className="variaty-modal"
			>
				<div className="modal-content">
					<div className="modal-body" style={styleModalBody}>
						<div className="modal-top">
							<p>{t("variety")}</p>
							<button
								className="closer-button"
								onClick={() => setOpenModalTwo({ open: false })}
							>
								<img
									src="/assets/images/vibo/BlackX.svg"
									alt="closer-img"
								/>
							</button>
							<div className="variations-discounts">
								<div className="product-img">
									<img
										src={getImageUrl(productImg)}
										alt="product-img"
									/>
								</div>
								<div className="row discounts">
									<GetPromotions
										product={product}
										promotion={promotion}
									/>
								</div>
							</div>
						</div>
						<div className="modal-section">
							{product?.data?.variation?.properties ? (
								<PropertiesView
									product={product}
									onComplated={onComplatedMiddle}
									selectVar={selectVar}
								/>
							) : null}
							<div className="product-quantity">
								<div className="details-filter-row details-row-size">
									<div className="product-details-quantity">
										<p>Выберите количество</p>
										<div>
											<InputChangerCount
												initValue={quantity}
												disable={false}
												onChange={value =>
													onChange(
														value,
														product.data.variation
															.id
													)
												}
												variationId={
													product.data.variation.id
												}
											/>
										</div>
									</div>
								</div>
							</div>
						</div>
						<div className="modal-footer d-block">
							<div className="price-count">
								<span>Стоимость отправки:</span>
								<p>
									{formatterCurrency(
										product.data.newPrice *
											(quantity > 0 ? quantity : 1)
									)}
									<img
										src="/assets/images/vibo/rightArrow.svg"
										alt=""
									/>
								</p>
							</div>
							<div className="countable-price">
								<span>
									Ценность продукции{" "}
									{/* <span>(0 тип, 0шт.)</span> */}
								</span>
								<p>
									{formatterCurrency(
										product.data.newPrice *
											(quantity > 0 ? quantity : 1)
									)}
								</p>
							</div>
							{isIEBrowser() || isEdgeBrowser() ? (
								<button
									className="btn"
									onClick={addToCartHandler}
									style={{ minHeight: "4rem" }}
								>
									{loading ? (
										<Loader />
									) : (
										<span>{t("add-cart")}</span>
									)}
								</button>
							) : (
								<button
									className="btn"
									disabled={!enable}
									onClick={addToCartHandler}
								>
									{loading ? (
										<Loader />
									) : (
										<span>{t("add-cart")}</span>
									)}
								</button>
							)}
						</div>
					</div>
				</div>
			</Modal>
		</div>
	);
}
