import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useSelector } from "react-redux";
import { getStaticPage } from "../../../api/staticPage";
import market from "../../../config/market";
import GenereteBody from "../../../views/static-page/GenereteBody";
import LoaderOverlay from "../loaders/LoaderOverlay";

export default function SizeTableModal({ modal, setModal }) {
	const { menuList } = useSelector((state) => state.menu);
	const sizeTable = menuList.find((m) => m.slug === "tablitsa-razmerov-1");

	const [page, setPage] = useState();
	const [loading, setLoading] = useState(true);

	const customStyles = {
		content: {
			maxWidth: "80%",
			top: "5%",
			margin: "0 auto",
			padding: "20px",
			overlay: "auto",
		},
		overlay: {
			backgroundColor: "rgba(77,77,77,0.6)",
			zIndex: "10000",
		},
	};

	useEffect(() => {
		if (sizeTable.id) {
			setLoading(true);
			getStaticPage(sizeTable.staticPage).then((res) => {
				setPage(res.content);
				setLoading(false);
			});
		}
	}, [sizeTable.id]);

	useEffect(() => {
		if (market.subdomain === "atlantamarket2" && page && modal.open) {
			setTimeout(() => {
				const scriptCode = page.split("<script>");
				eval(scriptCode[1].split("</script>")[0]);
			}, 100);
		}
	});

	return (
		<Modal
			isOpen={modal.open}
			onRequestClose={() => setModal({ open: false })}
			style={customStyles}
		>
			{loading ? <LoaderOverlay /> : <GenereteBody page={page} />}
		</Modal>
	);
}
