import React from "react";
import Modal from "react-modal";
import { dt } from "../../../i18n";
import { useTranslation } from "react-i18next";
export default function CharacteristicModal(props) {
	const { isOpen, setIsOpen, characteristics } = props;
	const { t } = useTranslation();
	const customStyles = {
		overlay: {
			backgroundColor: "rgba(77,77,77,0.6)",
			zIndex: "10000"
		}
	};
	const styleModalBody = {
		padding: "2rem"
	};

	return (
		<div>
			<Modal
				isOpen={isOpen.open}
				onRequestClose={() => setIsOpen({ open: false })}
				style={customStyles}
				className="characteristic-modal"
			>
				<div className="modal-content">
					<div className="modal-body" style={styleModalBody}>
						<div className="modal-top">
							<p> {t("characteristics")}</p>
							<button
								className="closer-button"
								onClick={() => setIsOpen({ open: false })}
							>
								<img
									src="/assets/images/vibo/BlackX.svg"
									alt="closer-img"
								/>
							</button>
						</div>
						<div className="modal-section">
							{characteristics.map(
								([propIndex, characteristic]) => {
									return (
										<span key={propIndex}>
											{dt(characteristic.name)}
											&nbsp;:&nbsp;
											{dt(characteristic.value)}
										</span>
									);
								}
							)}
						</div>
					</div>
				</div>
			</Modal>
		</div>
	);
}
