import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { addToCart } from "../../../actions/cart";
import ProductCard from "../../../components/features/product";
import OwlCarousel from "../../../components/features/owl-carousel";
import QuickView from "../../../components/features/product/QuickViewNew";
import ChooseVariationModal from "../../../components/features/modal/choose-variation";
import supplierProducts from "../../../api/supplierProducts";
import { useTranslation } from "react-i18next";
import ShimmerProducts from "../../../components/features/loaders/ShimmerProducts";
import QueryString from "qs";
import { likeProducts, relatedSlider2 } from "../../../components/options";

function RelatedProducts({ addToCart, supplierId, product }) {
	const { t } = useTranslation();
	let mount = true;

	const [relatedProducts, setRelatedProducts] = useState([]);
	const [loading, setLoading] = useState(true);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 1 } },
	});

	const initEffect = async () => {
		const { variations } = await supplierProducts(
			supplierId,
			QueryString.stringify({ page: 1, size: 8 })
		);
		if (mount) {
			setRelatedProducts(variations);
			setLoading(false);
		}
	};
	useEffect(() => {
		initEffect();
		return () => {
			mount = false;
		};
	}, [supplierId]);

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct,
		});
	}

	return (
		<div className="like-supplier-products">
			<h2 className="title mb-4 text-left">
				{t("supplier-like-products")}
			</h2>
			<div className="row">
				<div className="col-lg-12 col-md-12 col-12 mx-auto">
					{loading ? (
						<ShimmerProducts columns={10} option={likeProducts} />
					) : (
						<OwlCarousel
							carouselOptions={{
								...relatedSlider2,
								nav: false,
								dots: false,
								responsive: {
									0: {
										items: 2,
									},
									576: {
										items: 4,
									},
									992: {
										items: 6,
									},
								},
							}}
							carouselId="likeSuppliersProducts"
						>
							{relatedProducts.map((item, index) => (
								<ProductCard
									product={item}
									onAddToCart={(dataProduct) => {
										onAddToCart(dataProduct);
									}}
									key={`product-${index}`}
								/>
							))}
						</OwlCarousel>
					)}
					<QuickView />
					<ChooseVariationModal
						setModal={setModal}
						modal={modal}
						addToCart={addToCart}
					/>
				</div>
			</div>
		</div>
	);
}

export default connect(null, {
	addToCart,
})(RelatedProducts);
