import { Splide, SplideSlide } from "@splidejs/react-splide";
import React, { useEffect, useState } from "react";
import { searchByTags } from "../../api/products";
import ProductCard from "../../components/features/product";
import { relatedSliderTwo } from "../../components/options";
import { useTranslation } from "react-i18next";
import ChooseVariationModal from "../../components/features/modal/choose-variation";

export default function CrossSellProducts({ product, addToCart }) {
	if (!product && !product.product.crossSellTags) {
		return "";
	}
	const [crossProducts, setCrossProducts] = useState({ crossProducts: [] });
	const tags = product.product.crossSellTags;
	const { t } = useTranslation();
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 1 } },
	});
	useEffect(() => {
		if (tags) {
			var tagsArr = [];
			async function fetchData(tags) {
				await tags.forEach((item) => {
					tagsArr.push(`tags[]=${item}`);
				});
				await searchByTags(tagsArr.toString().replace(",", "&")).then(
					(res) => {
						setCrossProducts({
							crossProducts: [...res.variations],
						});
					}
				);
			}
			fetchData(tags);
		}
	}, [product]);

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct,
		});
	}

	return (
		<div className="cross-sell-products">
			<div className="container">
				<div className="row">
					<div className="col-12">
						<h2 className="title mt-4">
							<b>{t("cross-sell-products")}</b>
						</h2>
						<Splide
							options={relatedSliderTwo}
							carouselId="relatedProducts"
						>
							{crossProducts?.crossProducts
								?.slice(0, 20)
								.map((item, index) => (
									<SplideSlide key={`product-${index}`}>
										<ProductCard
											product={item}
											onAddToCart={(dataProduct) => {
												onAddToCart(dataProduct);
											}}
										/>
									</SplideSlide>
								))}
						</Splide>
					</div>
				</div>
			</div>
			<ChooseVariationModal
				setModal={setModal}
				modal={modal}
				addToCart={addToCart}
			/>
		</div>
	);
}
